<template>
  <div>
        <form>
            <p v-for="(item,key) in items">
              <span><span class="must">*</span> {{item.name}} <strong>:</strong></span>
              <input type="text" :placeholder="item.place" v-model="item.modal" @focus="enter(key)" @blur="out(key)" @input="input(key)" :disabled="show">
              <strong v-if="item.show"><Icon type="close-circled" color="red" size="20" style="margin-right: 0.05rem"></Icon> {{item.error}} </strong>
              <strong v-if="item.right"> <Icon type="checkmark-circled" size="20" color="green"></Icon> </strong>
            </p>
          </form>
  </div>
</template>
<script>
  import bread from '../bread.vue'
  export default {
    data () {
      return {
        show:true,
        wxPay:false,
        pay:false,
        items:[
          {
            name:"微信公众号AppID",
            place:"请输入微信公众号AppID",
            error:'请填写微信公众号AppID',
            modal:'',
            show:false,
            right:false
          },
          {
            name:"微信公众号AppSecret",
            place:"请输入微信公众号AppSecret",
            error:'请填写微信公众号AppSecret',
            modal:'',
            show:false,
            right:false
          },
          {
            name:"微信商户号",
            place:"请输入微信商户号",
            error:'请填写微信商户号',
            modal:'',
            show:false,
            right:false
          },{
            name:"微信商户密钥",
            place:"请输入微信商户密钥",
            error:'请填写微信商户密钥',
            modal:'',
            show:false,
            right:false
          }
        ],
      }
    },
    methods: {
      change(){
        this.publicData(1);
      },
      publicData(val){
        let input=document.getElementsByTagName('input');
        if(this.items[0].modal.trim()&&this.items[1].modal.trim()&&this.items[2].modal.trim()&&this.items[3].modal.trim()){
          this.post('system/Config/setSystemPay',{
            is_wx:1,
            appid:this.items[0].modal.trim(),
            appsecret:this.items[1].modal.trim(),
            mch_id:this.items[2].modal.trim(),
            mch_key:this.items[3].modal.trim(),
            id:val
          }).then(res=>{
            if(res.data.code===10000){
              alert(res.data.msg_test);
            }
          })
        }else{
          if(!this.items[0].modal.trim()){
            input[0].style.borderColor = 'red';
            this.items[0].show = true;
          }
          if(!this.items[1].modal.trim()){
            input[1].style.borderColor = 'red';
            this.items[1].show = true;
          }
          if(!this.items[2].modal.trim()){
            input[2].style.borderColor = 'red';
            this.items[2].show = true;
          }
          if(!this.items[3].modal.trim()){
            input[3].style.borderColor = 'red';
            this.items[3].show = true;
          }
        }
      },
//      取消检测
      cancelCheck(val){
        this.items[val].show=false;
      },
//      检测
      check(val){
        if(this.items[val].modal.trim()){
          this.items[val].show=false;
          this.items[val].right=true;
          event.target.style.borderColor='#bbbaba'
        }else{
          this.items[val].show=true;
          this.items[val].right=false;
          event.target.style.borderColor='red'
        }
      },
      save(){
        this.publicData(1);
      },
      cancel(){
        for(let i=0;i<this.items.length;i++){
          this.items[i].show=false;
          this.items[i].right=false;
          this.items[i].modal='';
        }
        let input=document.getElementsByTagName('input');
        for(let j=0;j<input.length;j++){
          input[j].style.borderColor='#bbbaba';
        }
      },
      enter(){
        event.target.style.borderColor='#4251b6'
      },
      out(val){
        switch (val){
          case 0:
            this.check(0);
            break;
          case 1:
            this.check(1);
            break;
          case 2:
            this.check(2);
            break;
          case 3:
            this.check(3);
            break;
        }
      },
      input(val){
        switch (val){
          case 0:
            this.cancelCheck(0);
            break;
          case 1:
            this.cancelCheck(1);
            break;
          case 2:
            this.cancelCheck(2);
            break;
          case 3:
            this.cancelCheck(3);
            break;
        }
      },
    },
    mounted(){

    },
    components:{bread}
  }
</script>
<style scoped>
  p{
    padding: 20px 0;
    display: flex;
    align-items: center;
  }
  p>span{
    display: inline-block;
    width: 175px;
    text-align: right;
    margin-right: 10px;
  }
  p>strong{
    display: flex;
    align-items: center;
    color: red;
    font-weight: 400;
    margin-left: 0.1rem;

  }
  .must{
    font-size: 15px;
    color: red;
    margin-right:10px;
  }
  input{
    display: inline-block;
    padding:8px 10px;
    border: 1px solid #bbbaba;
    width: 400px;
    border-radius:5px;
    /*background-color: #eeeeee;*/
  }
  .btn{
    padding: 20px 180px;
  }
  .save{
    display: inline-block;
    padding: 8px 35px;
    background-color: #ff8800;
    border-radius: 0.05rem;
    color: white;
  }
  .save:hover{text-decoration: none}
  .cancel{
    margin-left: 15px;
    display: inline-block;
    padding: 7px 34px;
    border: 1px solid #4251b6;
    border-radius: 5px;
    color:#4251b6 ;
  }
  .cancel:hover{
    background:#4251b6 ;
    color: white;
    text-decoration: none;
  }
</style>



