<template>
  <div>
    <div class="open">
      <div class="box">
        <span class="title"> 微信扫码支付: </span>
        <i-switch size="large"  v-model="wxPay" @on-change="change(0)" true-value="1" false-value="0">
          <span slot="open">开启</span>
          <span slot="close">关闭</span>
        </i-switch>
      </div>
      <div class="box">
        <span class="title"> 支付宝付款: </span>
        <i-switch size="large" v-model="pay" true-value="1" false-value="0" @on-change="change(1)">
          <span slot="open">开启</span>
          <span slot="close">关闭</span>
        </i-switch>
      </div>
    </div>
        <div class="pay">
          <form>
            <p v-for="(item,key) in items">
              <span><span class="must">*</span> {{item.name}} <strong>:</strong></span>
              <input type="text" :placeholder="item.place" v-model.trim="item.modal" @focus="enter(key)" @blur="out(key)" @input="input(key)" :disabled="wxShow" class="wxInput">
              <strong v-if="item.show"><!--<Icon type="close-circled" color="#e46751" size="20" style="margin-right:10px"></Icon>--> {{item.error}} </strong>
              <strong v-if="item.right"> <Icon type="checkmark-circled" size="20" color="green"></Icon> </strong>
            </p>
          </form>
        </div>
        <div class="line"></div>
        <div class="pay">
          <form>
            <p v-for="(item,key) in lists">
              <span><span class="must">*</span> {{item.name}} <strong>:</strong></span>
              <input type="text" :placeholder="item.place" v-model.trim="item.modal" @focus="payEnter(key)" @blur="payOut(key)" @input="payInput(key)" class="payInput" :disabled="payShow">
              <strong v-if="item.show"><!--<Icon type="close-circled" color="#e46751" size="20" style="margin-right:10px"></Icon>--> {{item.error}} </strong>
              <strong v-if="item.right"> <Icon type="checkmark-circled" size="20" color="green"></Icon> </strong>
            </p>
          </form>
        </div>
    <p class="btn">
      <a href="javascript:void(0)" class="save" @click="save" v-if="!show">保存</a>
      <a href="javascript:void(0)" class="save" @click="changeData" v-if="show">修改</a>
      <a href="javascript:void(0)" @click="cancel"  class="cancel">取消</a>
    </p>
  </div>
</template>
<script>
  import bread from '../bread.vue'
  import wxPay from './wxPay.vue'
  import zfbPay from './zfPay.vue'
  export default{
    data(){
      return {
        show:false,
        wxShow:true,
        payShow:true,
        wxPay:"0",
        pay:"0",
        items:[
          {
            name:"微信公众号AppID",
            place:"请输入微信公众号AppID",
            error:'请填写微信公众号AppID',
            modal:'',
            show:false,
            right:false
          },
          {
            name:"微信公众号AppSecret",
            place:"请输入微信公众号AppSecret",
            error:'请填写微信公众号AppSecret',
            modal:'',
            show:false,
            right:false
          },
          {
            name:"微信商户号",
            place:"请输入微信商户号",
            error:'请填写微信商户号',
            modal:'',
            show:false,
            right:false
          },{
            name:"微信商户密钥",
            place:"请输入微信商户密钥",
            error:'请填写微信商户密钥',
            modal:'',
            show:false,
            right:false
          }
        ],
        lists:[
          {
            name:"支付宝商户AppID",
            place:"请输入支付宝商户AppID",
            error:'请填写支付宝商户AppID',
            modal:'',
            show:false,
            right:false
          },
          {
            name:"支付宝私钥",
            place:"请输入支付宝私钥",
            error:'请填写支付宝私钥',
            modal:'',
            show:false,
            right:false
          },
          {
            name:"支付宝公钥",
            place:"请输入支付宝公钥",
            error:'请填写支付宝公钥',
            modal:'',
            show:false,
            right:false
          }
        ],
      }
    },
    methods:{
      save(){
        if(this.wxPay==='1' && this.pay === '1'){
           this.all();
        }else if(this.wxPay==='1' && this.pay==='0'){
          this.onlyWx()
        }else if(this.wxPay==='0' && this.pay==='1'){
          this.onlyPay();
        }else{
          alert('请开启在保存/修改数据')
        }
      },
      changeData(){
        this.publicAjax(1);
      },
      cancel(){
        if(this.wxPay==='1' && this.pay === '1'){
        this.cancelAll();
        }else if(this.wxPay==='1' && this.pay==='0'){
          this.cancelWx()
        }else if(this.wxPay==='0' && this.pay==='1'){
          this.cancelPay();
        }
      },
//  取消
      cancelAll(){
        for(let i=0;i<this.items.length;i++){
          this.items[i].show=false;
          this.items[i].right=false;
          this.items[i].modal='';
        }
        let input=document.getElementsByClassName('wxInput');
        for(let j=0;j<input.length;j++){
          input[j].style.borderColor='#EAEAEA';
        }
        for(let i=0;i<this.lists.length;i++){
          this.lists[i].show=false;
          this.lists[i].right=false;
          this.lists[i].modal='';
        }
        let payInput=document.getElementsByClassName('payInput');
        for(let j=0;j<payInput.length;j++){
          payInput[j].style.borderColor='#EAEAEA';
        }
      },
//取消微信
      cancelWx(){
        for(let i=0;i<this.items.length;i++){
          this.items[i].show=false;
          this.items[i].right=false;
          this.items[i].modal='';
        }
        let input=document.getElementsByClassName('wxInput');
        for(let j=0;j<input.length;j++){
          input[j].style.borderColor='#EAEAEA';
        }
      },
//取消支付宝
      cancelPay(){
        for(let i=0;i<this.lists.length;i++){
          this.lists[i].show=false;
          this.lists[i].right=false;
          this.lists[i].modal='';
        }
        let payInput=document.getElementsByClassName('payInput');
        for(let j=0;j<payInput.length;j++){
          payInput[j].style.borderColor='#EAEAEA';
        }
      },
//
      publicAjax(val){
        this.post('system/Config/setSystemPay',{
          is_wx:1,
          appid:this.items[0].modal,
          appsecret:this.items[1].modal,
          mch_id:this.items[2].modal,
          mch_key:this.items[3].modal,
          ali_appid:this.lists[0].modal,
          ali_private_key:this.lists[1].modal,
          ali_public_key:this.lists[2].modal,
          is_alipay:1,
          id:val
        }).then(res=>{
          if(res.data.code===10000){
            alert(res.data.msg_test);
          }
        })
      },
//      支付宝微信都开启
      all(val){
        if(this.items[0].modal &&this.items[1].modal&&this.items[2].modal&&this.items[3].modal&&this.lists[0].modal&&this.lists[1].modal&&this.lists[2].modal){
              this.publicAjax(val);
        }else{
          let wxInput=document.getElementsByClassName('wxInput');
          let payInput=document.getElementsByClassName('payInput');
          if(!this.items[0].modal){
            wxInput[0].style.borderColor = '#e46751';
            this.items[0].show = true;
          }
          if(!this.items[1].modal){
            wxInput[1].style.borderColor = '#e46751';
            this.items[1].show = true;
          }
          if(!this.items[2].modal){
            wxInput[2].style.borderColor = '#e46751';
            this.items[2].show = true;
          }
          if(!this.items[3].modal){
            wxInput[3].style.borderColor = '#e46751';
            this.items[3].show = true;
          }
          if(!this.lists[0].modal){
            payInput[0].style.borderColor = '#e46751';
            this.lists[0].show = true;
          }
          if(!this.lists[1].modal){
            payInput[1].style.borderColor = '#e46751';
            this.lists[1].show = true;
          }
          if(!this.lists[2].modal){
            payInput[2].style.borderColor = '#e46751';
            this.lists[2].show = true;
          }
        }
      },
//      开启微信
      onlyWx(val){
        if(this.items[0].modal &&this.items[1].modal&&this.items[2].modal&&this.items[3].modal){
            this.publicAjax(val);
        }else{
          let wxInput=document.getElementsByClassName('wxInput');
          if(!this.items[0].modal){
            wxInput[0].style.borderColor = '#e46751';
            this.items[0].show = true;
          }
          if(!this.items[1].modal){
            wxInput[1].style.borderColor = '#e46751';
            this.items[1].show = true;
          }
          if(!this.items[2].modal){
            wxInput[2].style.borderColor = '#e46751';
            this.items[2].show = true;
          }
          if(!this.items[3].modal){
            wxInput[3].style.borderColor = '#e46751';
            this.items[3].show = true;
          }
        }
      },
//      开启支付宝
      onlyPay(val){
          if(this.lists[0].modal&&this.lists[1].modal&&this.lists[2].modal){
            this.publicAjax(val);
          }else{
            let payInput=document.getElementsByClassName('payInput');
            if(!this.lists[0].modal){
              payInput[0].style.borderColor = '#e46751';
              this.lists[0].show = true;
            }
            if(!this.lists[1].modal){
              payInput[1].style.borderColor = '#e46751';
              this.lists[1].show = true;
            }
            if(!this.lists[2].modal){
              payInput[2].style.borderColor = '#e46751';
              this.lists[2].show = true;
            }
          }
      },
//      取消检测
      cancelCheck(val){
        this.items[val].show=false;
      },
      check(val){
        if(this.items[val].modal){
          this.items[val].show=false;
          this.items[val].right=true;
          event.target.style.borderColor='#EAEAEA'
        }else{
          this.items[val].show=true;
          this.items[val].right=false;
          event.target.style.borderColor='#e46751'
        }
      },
      enter(){
        event.target.style.borderColor='#6098FF'
      },
      out(val){
        switch (val){
          case 0:
            this.check(0);
            break;
          case 1:
            this.check(1);
            break;
          case 2:
            this.check(2);
            break;
          case 3:
            this.check(3);
            break;
        }
      },
      input(val){
        switch (val){
          case 0:
            this.cancelCheck(0);
            break;
          case 1:
            this.cancelCheck(1);
            break;
          case 2:
            this.cancelCheck(2);
            break;
          case 3:
            this.cancelCheck(3);
            break;
        }
      },
      change(val){
          if(val){
            this.payShow=!this.payShow;
          }else{
            this.wxShow=!this.wxShow
          }
      },
//      支付宝
      cancelCheckPay(val){
        this.lists[val].show=false;
      },
      checkPay(val){
        if(this.lists[val].modal){
          this.lists[val].show=false;
          this.lists[val].right=true;
          event.target.style.borderColor='#EAEAEA'
        }else{
          this.lists[val].show=true;
          this.lists[val].right=false;
          event.target.style.borderColor='#e46751'
        }
      },
      payEnter(){
        event.target.style.borderColor='#6098FF'
      },
      payOut(val){
        switch (val){
          case 0:
            this.checkPay(0);
            break;
          case 1:
            this.checkPay(1);
            break;
          case 2:
            this.checkPay(2);
            break;
        }
      },
      payInput(val){
        switch (val){
          case 0:
            this.cancelCheckPay(0);
            break;
          case 1:
            this.cancelCheckPay(1);
            break;
          case 2:
            this.cancelCheckPay(2);
            break;
        }
      },
    },
    mounted(){
      this.post("system/Config/getSystemPay",{id:1}).then(res=>{
        if(res.data.code===10000){
          this.wxPay=`${res.data.data.is_wx}`;
          this.pay=`${res.data.data.is_alipay}`;
          this.show=true;
        }
      })
    },
    components:{bread,wxPay,zfbPay}
  }
</script>
<style scoped>
  .open{
      display: flex;
  }
  .open>div:nth-child(2){
    margin: 0 20px;
  }
  .pay{
    margin-top: 20px;
    margin-bottom: 40px;
  }
    .title{
      display: inline-block;
      font-size: 16px;
      padding: 15px 0;
      margin-right:20px;
    }
    .box{
      margin-left: 10px;
      display: flex;
      align-items: center;
    }
  .line{
    border-bottom: 1px solid #eeeeee;
  }
  p{
    padding: 3px 0;
    display: flex;
    align-items: center;
  }
  p>span{
    display: inline-block;
    width: 175px;
    text-align: right;
    margin-right: 20px;
  }
  p>strong{
    display: flex;
    align-items: center;
    color: #e46751;
    font-weight: 400;
    margin-left: 10px;

  }
  .must{
    font-size: 15px;
    color: #e46751;
    margin-right:10px;
  }
  input{
    display: inline-block;
    padding:8px 10px;
    border: 1px solid #EAEAEA;
    width: 400px;
    border-radius:5px;
    background-color: #FFFFFF;
  }
  .btn{
    padding: 0px 197px;
  }
  .save{
    display: inline-block;
    padding: 8px 35px;
    background-color: #6098FF;
    border-radius:5px;
    color: white;
  }
   .save:hover{
    text-decoration: none;
  }
  .cancel{
    margin-left: 15px;
    display: inline-block;
    padding: 7px 34px;
    border: 1px solid #777777;
    border-radius: 5px;
    color:#777777;
  }
  .cancel:hover{
    background:#799EE3 ;
    color: white;
    text-decoration:none;
    border: none;
  }
</style>



