<template>
  <div>
    <form>
      <p v-for="(item,key) in items">
        <span><span class="must">*</span> {{item.name}} <strong>:</strong></span>
        <input type="text" :placeholder="item.place" v-model="item.modal" @focus="enter(key)" @blur="out(key)" @input="input(key)" class="input" :disabled="payShow">
        <strong v-if="item.show"><Icon type="close-circled" color="red" size="20" style="margin-right: 0.05rem"></Icon> {{item.error}} </strong>
        <strong v-if="item.right"> <Icon type="checkmark-circled" size="20" color="green"></Icon> </strong>
      </p>
    </form>
  </div>
</template>
<script>
  import bread from '../bread.vue'
  export default {
    data () {
      return {
        payShow:true,
        wxPay:false,
        pay:false,
        items:[
          {
            name:"支付宝商户AppID",
            place:"请输入支付宝商户AppID",
            error:'请填写支付宝商户AppID',
            modal:'',
            show:false,
            right:false
          },
          {
            name:"支付宝私钥",
            place:"请输入支付宝私钥",
            error:'请填写支付宝私钥',
            modal:'',
            show:false,
            right:false
          },
          {
            name:"支付宝公钥",
            place:"请输入支付宝公钥",
            error:'请填写支付宝公钥',
            modal:'',
            show:false,
            right:false
          }
        ],
      }
    },
    methods: {
      publicData(val){
        let input=document.getElementsByClassName('input');
        if(this.items[0].modal.trim()&&this.items[1].modal.trim()&&this.items[2].modal.trim()&&this.items[3].modal.trim()){
          this.post('system/Config/setSystemPay',{
            ali_appid:this.items[0].modal.trim(),
            ali_private_key:this.items[1].modal.trim(),
            ali_public_key:this.items[2].modal.trim(),
            is_alipay:1,
            id:val
          }).then(res=>{
            console.log(res);
          })
        }else{
          if(!this.items[0].modal.trim()){
            input[0].style.borderColor = 'red';
            this.items[0].show = true;
          }
          if(!this.items[1].modal.trim()){
            input[1].style.borderColor = 'red';
            this.items[1].show = true;
          }
          if(!this.items[2].modal.trim()){
            input[2].style.borderColor = 'red';
            this.items[2].show = true;
          }
        }
      },
    change(){
           this.publicData(1)
    },
//      取消检测
      cancelCheck(val){
        this.items[val].show=false;
      },
//      检测
      check(val){
        if(this.items[val].modal.trim()){
          this.items[val].show=false;
          this.items[val].right=true;
          event.target.style.borderColor='#bbbaba'
        }else{
          this.items[val].show=true;
          this.items[val].right=false;
          event.target.style.borderColor='red'
        }
      },
      save(){
        this.publicData();
      },
      cancel(){
        for(let i=0;i<this.items.length;i++){
          this.items[i].show=false;
          this.items[i].right=false;
          this.items[i].modal='';
        }
        let input=document.getElementsByClassName('input');
        for(let j=0;j<input.length;j++){
          input[j].style.borderColor='#bbbaba';
        }
      },
      enter(){
        event.target.style.borderColor='#4251b6'
      },
      out(val){
        switch (val){
          case 0:
            this.check(0);
            break;
          case 1:
            this.check(1);
            break;
          case 2:
            this.check(2);
            break;
          case 3:
            this.check(3);
            break;
        }
      },
      input(val){
        switch (val){
          case 0:
            this.cancelCheck(0);
            break;
          case 1:
            this.cancelCheck(1);
            break;
          case 2:
            this.cancelCheck(2);
            break;
          case 3:
            this.cancelCheck(3);
            break;
        }
      },
    },
    mounted(){

    },
    components:{bread}
  }
</script>
<style scoped>
  p{
    padding: 20px 0;
    display: flex;
    align-items: center;
  }
  p>span{
    display: inline-block;
    width: 175px;
    text-align: right;
    margin-right:10px;
  }
  p>strong{
    display: flex;
    align-items: center;
    color: red;
    font-weight: 400;
    margin-left:10px;

  }
  .must{
    font-size: 15px;
    color: red;
    margin-right:10px
  }
  input{
    display: inline-block;
    padding: 8px 10px;
    border: 1px solid #bbbaba;
    width: 400px;
    border-radius: 5px;
    /*background-color: #eeeeee;*/
  }
</style>



